import React, { Component } from "react";
import "./App.css";
import CountryMap from "./components/CountryMap";
import {
  HashRouter as Router,
  Route,
  Switch,
  Redirect,
  Link
} from "react-router-dom";

class App extends Component {
  render() {
    return (
      <Router>
        <div className="App">
          <div className="navigation">
            <ul>
              <Link to={"/belgium/communautes/"} replace>
                Communautés
              </Link>
              <Link to={"/belgium/regions/region-wallonne"} replace>
                Régions
              </Link>
              <Link to={"/belgium/provinces/namur"} replace>
                Provinces
              </Link>
            </ul>
          </div>
          <Switch>
            <Route
              path="/belgium/:type/:zone"
              render={props => {
                return (
                  <CountryMap
                    key={props.match.params.type}
                    {...props}
                    type={props.match.params.type}
                    zone={props.match.params.zone}
                  />
                );
              }}
            />

            <Route
              path="/belgium/communautes"
              render={props => {
                return (
                  <CountryMap
                    key="communautes"
                    {...props}
                    type="communautes"
                    zone=""
                  />
                );
              }}
            />

            <Route
              render={() => <Redirect to="/belgium/regions/region-flamande" />}
            />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
